



























































































import loadMore from '../../../components/load-more.vue'
export default {
	data(){
		return{
			type:1,
			toast:false,
			indexs:'-1',
			orderState:'1',
			orderState1:'1',
			selected:1,
			isActive:true,
			selecteds:2,
			selecteds1:3,
			selecteds2:4,
			list: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	methods:{
		// 订单类型切换
		changOrder(e){
			var el=e.currentTarget;
			this.orderState=el.dataset.state;
			console.log(this.orderState)
			if(this.orderState==1){
				this.orderState1=2;
				return;
			}else if(this.orderState==2){
				this.orderState1=1;
				this.orderState=1;
				console.log(this.orderState+'-----2')
				
			}
		},
		changOrder1(e){
			var el=e.currentTarget;
			this.orderState1=el.dataset.states;
			console.log(this.orderState1)
			if(this.orderState1==1){
				this.orderState=2;
				this.orderState1=2;
				return;
			}else if(this.orderState1==2){
				this.orderState=2;
				console.log(this.orderState+'----1');
				return;
			}
		},
		// 切换样式
		change(e){
			var el=e.currentTarget;
			this.type=el.dataset.type;
			this.indexs='-1'
		},
		// 列表的点击选中
		selectDel(e){
			var el=e.currentTarget;
			this.indexs=el.dataset.indexs;
			console.log(e)
		},
		change1(e){
			var el=e.currentTarget;
			this.selected=el.dataset.selected;
			// 按时间顺序分
			if(this.selected==1){
				if(this.selecteds===2){
					this.selecteds=5
				}else{
					this.selecteds=2
				}
			}
			// 按数量分
			if(this.selected==2){
				if(this.selecteds1===3){
					this.selecteds1=6
				}else{
					this.selecteds1=3
				}
			}
			// 按单价分
			if(this.selected==3){
				if(this.selecteds2===4){
					this.selecteds2=7
				}else{
					this.selecteds2=4
				}
			}
		},
		// 滚动到底部会触发的时间
		lower(e) {
			console.log(e);
			var page1=10
			this.page++;
			if(this.page>this.totalPage){
				uni.showToast({
					title: '暂无更多!',
					duration:2000,
					icon:'none'
				});
				return;
			}else{
				uni.showLoading({
					title: '加载中'
				});
				this.list.push(this.list)
				setTimeout(function () {
					uni.hideLoading();
				}, 2000);
			}
		},
		confrim(){
			this.toast=false;
		},
		cancel(){
			this.toast=false;
		},
		// 显示弹窗
		delOrde(){
			this.toast=true
		}
	},
	onLoad() {
		let list = [];
		for (let i = 1; i < 11; i++) {
			list.push(i);
		}
		this.list = list;
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let list = [],
			maxItem = this.list[this.list.length - 1],
			length = maxItem + 6;
		for (let i = maxItem + 1; i < length; i++) {
			list.push(i);
		}
		setTimeout(() => {
			if (length > 26) {
				this.loadingType = 2;
				return;
			}
			this.list = this.list.concat(list);
			this.loadingType = 0;
		}, 1500);
	},
	components: {
		loadMore
	}
}



